.project-modal {
    position: absolute;
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px;
    max-width: 900px;
    color: #19281c;
    margin: 0 auto;
    border: 2px solid #18261D;
    background: #bdbdbd;
    overflow: auto;
    border-radius: 4px;
    outline: none;
    padding: 25px;
}

.ReactModal__Body--open {
    overflow: hidden;
}

.project-details {
    height: 100%;
    overflow: auto;
}

.project-detail-header {
    font-size: 1.8em;
    font-weight: 700;
    text-align: center;
}

.project-detail-image {
    display: block;
    width: 100%;
    max-width: 630px;
    margin: 30px auto;
    border: 1px solid #18261D;
    border-radius: 6px;
}

.project-detail-tech h3 {
    margin: 10px 0;
    font-size: 1.2em;
    font-weight: 700;
}


.home-button {
    position: absolute;
    top: 10px;
    right: 10px;
    border: none;
    border-radius: 11px;
    color: white;
    background: grey;
    cursor: pointer;
}

.home-button:hover {
    color: #E91326;
}
